/* General Styles */
body {
  font-family: 'Helvetica Neue', Arial, sans-serif;
  background-color: #f0f1f2;
  margin: 0;
  padding: 0;
}

/* Container for Tiles */
.tiles {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* Center align tiles */
  padding: 40px;
}

/* Individual Tile */
.tile {
  border-radius: 12px;
  overflow: hidden;
  width: 280px;
  margin: 15px; /* Adjust spacing between tiles */
  text-align: center;
  box-shadow: 0 8px 16px rgba(0,0,0,0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.tile:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 24px rgba(0,0,0,0.15);
}

/* Tile Content */
.tile-content {
  padding: 20px;
}

.tile-content img {
  width: 100%;
  height: auto;
  display: block;
}

/* Text Content */
.text-content {
  padding: 20px 0;
  background-color: #ffffff;
}

.text-content h3 {
  margin-bottom: 10px;
  font-size: 1.25rem;
  color: #333333;
  font-weight: bold;
}

.text-content button {
  padding: 12px 24px;
  font-size: 16px;
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.text-content button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

/* Product List */
.product-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  padding: 40px;
}

/* Product Item */
.product-item {
  border-radius: 12px;
  overflow: hidden;
  width: 250px;
  text-align: center;
  padding: 20px;
  box-shadow: 0 8px 16px rgba(0,0,0,0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.product-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 24px rgba(0,0,0,0.15);
}

.product-item img {
  width: 100%;
  height: auto;
  display: block;
  margin-bottom: 10px;
}

/* Product Item Text */
.product-item h3 {
  margin: 10px 0;
  font-size: 1.15rem;
  color: #333333;
  font-weight: bold;
}

.product-item p {
  margin: 5px 0;
  color: #555555;
}

.product-item button {
  padding: 12px 24px;
  font-size: 16px;
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.product-item button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}
