/* Navbar Container */
.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff; /* White background for a clean look */
  padding: 10px 20px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1); /* Subtle shadow for a modern touch */
}

/* Navbar List */
.navbar ul {
  list-style-type: none;
  display: flex;
  padding: 0;
  margin: 0;
}

/* Navbar List Items */
.navbar li {
  margin: 0 20px;
}

/* Navbar Links */
.navbar a {
  text-decoration: none;
  color: #333; /* Dark gray text color */
  font-weight: normal; /* Default font weight */
  transition: color 0.3s ease, font-weight 0.3s ease; /* Smooth transition for color and font-weight */
}

/* Hover Effect for Navbar Links */
.navbar a:hover {
  color: #007bff; /* Change text color on hover */
}

/* Active Link Styling */
.navbar .active a {
  font-weight: bold; /* Make text bold for the active link */
  color: #007bff; /* Optional: Change color to match the active link indicator */
}
